 * Replace them with your own styles or remove the file completely.
 */
.post-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 50px; }

.card {
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px;
  padding: 10px; }

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); }

.card_img {
  margin: auto;
  border-radius: 5px; }

.card_title {
  text-align: center; }

.card_link:hover {
  color: #8224e3; }

.card_excerpt {
  text-align: center; }

.card_cat_list {
  text-align: right;
  margin: 10px; }
